// $background-url: url("/portfolio/images/about_bg.png");
$background-url: url("/images/about_bg.png");

/* Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
    .home-container {
        background: $background-url;
        background-position: center center;
        background-attachment: fixed;
        height: calc(100vh - 66px);
        width: 100vw;
        background-size: cover;
    }

    .intro-container {
        position: absolute;
        top: 30%;
        left: 20%;
        width: 240px;
    }

    .intro-line-1 {
        color: white;
        font-family: "Caveat", cursive;
        font-size: 25px;
    }

    #change-letter {
        color: red;
        font-family: "Caveat", cursive;
        font-size: 25px;
    }
}

