//------------ Text changing animation ---------------

#change-letter::after {
    content: "";
    animation: changeLetter 2s linear infinite;
}

@keyframes changeLetter {
    0% {
        content: "software engineer";
    }
    100% {
        content: "frontend developer";
    }
}

